import {
  
  MAKE_PAYMENT_START,
  MAKE_PAYMENT_SUCCESS,
  MAKE_PAYMENT_ERROR,

  
} from '../actions/ActionTypes';

const INITIAL_STATE = {
  ACTION_TYPE :"",

  isMakePaymentLoading: false,
  makePaymentError:"",

  payToken:"",
  payTokenUrl:"https://app.slydepay.com/paylive/detailsnew.aspx?pay_token={pay_Token}",
  paymentCode: "" ,
  qrCodeUrl: "", 
}


const PaymentReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };


  switch (action.type) {

    
    case MAKE_PAYMENT_START:

      newState.isMakePaymentLoading = true;
      newState.makePaymentError="";
      newState.payToken= "";
      newState.payTokenUrl ="";
      newState.paymentCode="";
      newState.qrCodeUrl= "";

      newState.ACTION_TYPE = action.type;
    
      return newState;

    case MAKE_PAYMENT_SUCCESS:

      newState.isMakePaymentLoading = false;
      newState.makePaymentError="";

      newState.payToken= action.payToken;
      newState.paymentCode= action.paymentCode;
      newState.qrCodeUrl= action.qrCodeUrl;
      newState.payTokenUrl =`https://app.slydepay.com/paylive/detailsnew.aspx?pay_token=${action.payToken}`;
      
      newState.ACTION_TYPE = action.type;

      return newState;

    case MAKE_PAYMENT_ERROR:                
      
      newState.isMakePaymentLoading = false;
      newState.makePaymentError= action.error;
      newState.ACTION_TYPE = action.type;

      return newState;
    
    default:
      return newState;


  }
};
export default PaymentReducer;