import {
  
  GET_AVAILABLE_CARS_START,
  GET_AVAILABLE_CARS_SUCCESS,
  GET_AVAILABLE_CARS_ERROR
  
} from '../actions/ActionTypes';

const INITIAL_STATE = {
  isAvailableCarsLoading: false,    
  availableCarsError: "",    
  categories :[],
  vehicles :[]
  
}


const AvailableCarsReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };


  switch (action.type) {

        case GET_AVAILABLE_CARS_START:

      newState.isAvailableCarsLoading = true;
      newState.availableCarsError="";

      newState.categories= [];
      newState.vehicles= [];


      return newState;

    case GET_AVAILABLE_CARS_SUCCESS:

      newState.isAvailableCarsLoading = false;
      newState.availableCarsError="";
      newState.categories= action.categories;
      newState.vehicles= action.vehicles;
      
      return newState;

    case GET_AVAILABLE_CARS_ERROR:    
                  
      newState.isAvailableCarsLoading = false;
      newState.availableCarsError= action.error;
            
      return newState;

    default:
      return newState;


  }
};
export default AvailableCarsReducer;