import React from "react";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Col, Container,
  Row,Spinner
} from "reactstrap";
import { PaymentTable, ReservationTable } from "../../components/ReservationSummaryComponents";
import { RESERVATION_TYPES } from "../../constants/reservation";
import { MAKE_PAYMENT_SUCCESS, SAVE_BOOKING_SUCCESS } from '../../redux/actions/ActionTypes';
import { updateSaveBookingGuest } from "../../redux/actions/BookingActions";
import { saveBookingMiddleware } from '../../redux/middleware/BookingMiddleware';
import { makePaymentMiddleware } from '../../redux/middleware/PaymentMiddleware';



function ReservationSummaryPage(props) {
  //this page will be used by both guest and authenticated user
  const routePath = (props.isGuest) ? "guest" : "user";

  //redux
  const dispatch = useDispatch();
  const bookingState = useSelector(state => state.booking);
  const paymentState = useSelector(state => state.payment);
  const authenticateState = useSelector(state => state.authenticate);

  const [firstName, setfirstName] = React.useState("");
  const [lastName, setLastName] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [individual, setIndividual] = React.useState(true);
  const [corporate, setCorporate] = React.useState(false);
  const [accountType, setAccountType] = React.useState("Individual");
  
  React.useEffect(() => {
    console.log(bookingState.ACTION_TYPE);
    if (bookingState.ACTION_TYPE === SAVE_BOOKING_SUCCESS) {
      //refresh bookings
      // dispatch(getBookingsMiddleware(authenticateState.customer_id))


      //DONT CALL MAKE_PAYMENT Middleware IF BOOKING TYPE IS FINANCED LEASE, just go to bookings page
      if (bookingState.saveBooking.rtype === RESERVATION_TYPES.FINANCE_LEASE) {
        alert("Booking Saved successfully!\nWara Drive will contact you shortly")
        props.history.push(`/${routePath}/my-booking-summary/`+bookingState.bookingId)
        
      } else {
        //call make payment if not financed lease
        dispatch(makePaymentMiddleware(bookingState.bookingId, authenticateState.customer.firstname,
          authenticateState.customer.email, bookingState.saveBooking.ccars[0].price.total))
      }
    }

  }, [bookingState.ACTION_TYPE])


  React.useEffect(() => {
    console.log(paymentState.ACTION_TYPE);

    //Go to browser if payment successfull
    if (paymentState.ACTION_TYPE === MAKE_PAYMENT_SUCCESS) {

      var win = window.open(paymentState.payTokenUrl, '_blank');
      win.focus();
      //redirect user to browser
      // Alert.alert("Payment Processing", "Click OK to complete payment!",
      //   [{
      //     text: "OK", onPress: () => {
      //       props.navigation.popToTop();
      //       // props.navigation.goBack(null);
      //       Linking.openURL(paymentState.payTokenUrl)
      //     }
      //   }],
      //   { cancelable: false });
      // ;

    }

  }, [paymentState.ACTION_TYPE])


  const onSaveBooking = () => {
    dispatch(
      updateSaveBookingGuest(
        firstName, //"firstname"	
        lastName,	//"lastname"
	      email, //"email" 
	      phone,//"phone"
        accountType,//"ctype"
        null, //reservation type
        null, //service type
        null, //start time
        null, //end time
        null, //pickup location
        null, //pick up gps
        null, //drop off location
        null, //dropoff gps
        null, //distace in km
        null, //hours
        null, //comments
        null, //total amount
        null //selected car(s}
      )
    );
    dispatch(  saveBookingMiddleware(bookingState.saveBooking) );
  }
  return (
    <Container fluid style={{ padding: 20 }}>
      <Row>
        <Col md="6" sm="12" style={wrapper}>
          <Row style={caption}>Reservation Summary</Row>

            <ReservationTable item={bookingState} />
        </Col>
        <Col style={wrapper}>
          <Row style={caption}>Payment Summary</Row>
            <PaymentTable item={bookingState} />
          <Row style={footer}>
            <Button href={`/${routePath}/`} >Cancel Booking</Button>
            
            <Button  onClick={onSaveBooking} style={button}>{(bookingState.isSaveBookingLoading ||paymentState.isMakePaymentLoading ) ? <Spinner color="light" /> : 'Proceed to Payment'}</Button>
          </Row>
        </Col>
      </Row>
    </Container>
  );
}

const wrapper = {

  borderColor: "grey",
  borderWidth: 0.1,
};

const caption = {
  display: "flex",
  justifyContent: "center",
  padding: 10,
  fontSize: 20,
};

const footer = {
    // borderStyle: "solid",
    justifyContent: "space-around"
};

const button={
    backgroundColor:'#CEAE57',
    borderWidth:0
}

export default ReservationSummaryPage;
