import {

    GET_PROFILE_START,
    GET_PROFILE_SUCCESS,
    GET_PROFILE_ERROR,
    
    UPDATE_LICENSE_START,
    UPDATE_LICENSE_SUCCESS,
    UPDATE_LICENSE_ERROR

} from './ActionTypes';


export const getProfileStart = () => (
    {
        type: GET_PROFILE_START  ,           
    }
)
export const getProfileSuccess = ( customer) => (
    {
        type: GET_PROFILE_SUCCESS  ,       
        customer: customer    
    }
)
export const getProfileError = (errorMsg) => (
    {
        type: GET_PROFILE_ERROR  , 
        error: errorMsg          
    }
)

export const updateLicenseStart = () => (
    {
        type: UPDATE_LICENSE_START  ,           
    }
)
export const updateLicenseSuccess = ( message) => (
    {
        type: UPDATE_LICENSE_SUCCESS  ,       
        error: message    
    }
)
export const updateLicenseError = (errorMsg) => (
    {
        type: UPDATE_LICENSE_ERROR  , 
        error: errorMsg          
    }
)
