import {

    GET_BOOKINGS_START,
    GET_BOOKINGS_SUCCESS,
    GET_BOOKINGS_ERROR,

    GET_BOOKING_START,
    GET_BOOKING_SUCCESS,
    GET_BOOKING_ERROR,

    SAVE_BOOKING_START,
    SAVE_BOOKING_SUCCESS,
    SAVE_BOOKING_ERROR,


    UPDATE_SAVE_BOOKING,
    RESET_SAVE_BOOKING

} from './ActionTypes';


export const getBookingsStart = () => (
    {
        type: GET_BOOKINGS_START,
    }
)
export const getBookingsSuccess = (bookings) => (
    {
        type: GET_BOOKINGS_SUCCESS,
        bookings: bookings,
    }
)
export const getBookingsError = (errorMsg) => (
    {
        type: GET_BOOKINGS_ERROR,
        error: errorMsg
    }
)

export const getBookingStart = () => (
    {
        type: GET_BOOKING_START,
    }
)
export const getBookingSuccess = (booking, cardetails) => (
    {
        type: GET_BOOKING_SUCCESS,
        booking: booking,
        cardetails: cardetails
    }
)
export const getBookingError = (errorMsg) => (
    {
        type: GET_BOOKING_ERROR,
        error: errorMsg
    }
)

//SAVE BOOKING ACTIONS


export const saveBookingStart = () => (
    {
        type: SAVE_BOOKING_START,
    }
)
export const saveBookingSuccess = (bookingId) => (
    {
        type: SAVE_BOOKING_SUCCESS,
        bookingId: bookingId

    }
)
export const saveBookingError = (errorMsg) => (
    {
        type: SAVE_BOOKING_ERROR,
        error: errorMsg
    }
)

//UPDATE SAVE BOOKING
export const updateSaveBooking = (customerId, reservatioinType, serviceType, startTime, endTime,
    pickUpText, pickUpGPS, dropOffText, dropOffGPS, distance,
    hours, comments, totalAmount, selectedCarsArray) => (
    {
        type: UPDATE_SAVE_BOOKING,

        customer: customerId,
        rtype: reservatioinType,
        stype: serviceType,
        starttime: startTime,
        endtime: endTime,
        pickup: pickUpText,
        pickupgps: pickUpGPS, //"5.7141674,-0.15418",
        dropoff: dropOffText,//"Kotoka International Airport",
        dropoffgps: dropOffGPS, // "5.606095499999999,-0.1681235",
        distance: distance, // "15.5 km",
        hours: hours,
        comments: comments,
        total_amt: totalAmount,
        ccars: selectedCarsArray
    }
)
export const updateSaveBookingGuest = (
    firstName, lastName, email, phone,ctype, 
    reservatioinType, serviceType, startTime, endTime,
    pickUpText, pickUpGPS, dropOffText, dropOffGPS, distance,
    hours, comments, totalAmount, selectedCarsArray) => (
    {
        type: UPDATE_SAVE_BOOKING,

        firstname: firstName,
        lastname: lastName,
        email: email,
        phone: phone,
        ctype: ctype,
        rtype: reservatioinType,
        stype: serviceType,
        starttime: startTime,
        endtime: endTime,
        pickup: pickUpText,
        pickupgps: pickUpGPS, //"5.7141674,-0.15418",
        dropoff: dropOffText,//"Kotoka International Airport",
        dropoffgps: dropOffGPS, // "5.606095499999999,-0.1681235",
        distance: distance, // "15.5 km",
        hours: hours,
        comments: comments,
        total_amt: totalAmount,
        ccars: selectedCarsArray
    }
)
export const resetSaveBooking = () => (
    {
        type: RESET_SAVE_BOOKING
    }
)