import {

    getBookingsStart,
    getBookingsSuccess,
    getBookingsError,

    getBookingStart,
    getBookingSuccess,
    getBookingError,
    saveBookingSuccess,
    saveBookingError,
    saveBookingStart,
    

} from '../actions/BookingActions';

import URLS from '../../config/urls';
import axios from 'axios'


// import * as AxiosLogger from 'axios-logger';

//REMOVE FROM PROD
// axios.interceptors.request.use(AxiosLogger.requestLogger);
// axios.interceptors.response.use(AxiosLogger.responseLogger);

export const getBookingsMiddleware = (customerId) => {
    return function (dispatch) {



        dispatch(getBookingsStart());

        return axios.post(
            URLS.GET_BOOKINGS_URL,

            { customer: customerId }

        ).then(function (response) {

          

            if (response.data.status === 'success') {

                dispatch(getBookingsSuccess(response.data.bookings))



            } else {

                dispatch(getBookingsError(response.data.message));
            }


        }).catch(function (error) {


            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(getBookingsError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(getBookingsError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(getBookingsError(error.message));


            }

        });
    };

}


export const getBookingMiddleware = (bookingId) => {
    return function (dispatch) {



        dispatch(getBookingStart());

        return axios.post(
            URLS.GET_BOOKING_URL,

            { id: bookingId }

        ).then(function (response) {

            

            if (response.data.status === 'success') {

                dispatch(getBookingSuccess(response.data.booking, response.data.cardetails[0]))



            } else {

                dispatch(getBookingError(response.data.message));
            }


        }).catch(function (error) {


            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(getBookingError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(getBookingError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(getBookingError(error.message));


            }

        });
    };

}



export const saveBookingMiddleware = (saveBookingObject ) => {
    return function (dispatch) {



        dispatch(saveBookingStart());

        return axios.post(
            URLS.SAVE_BOOKING_URL,

            saveBookingObject

        ).then(function (response) {

            // console.log(response)
            /*
                {
                    "status": "success",
                    "booking_id": "25261053e6d411ea"
                }
            */

            /*
            {
                "status": "failed",
                "message": "The vehicle selected is no longer available for period selected",
                "next_available": "July 18, 2020 at 08 04 PM"
            }
            */


            if (response.data.status === 'success') {

                dispatch(saveBookingSuccess( response.data.booking_id ));



            } else {

                dispatch(saveBookingError(response.data.message));
            }


        }).catch(function (error) {

            console.log(error);
            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx
                console.log(error.response);

                dispatch(saveBookingError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js

                console.log(error.request);
                dispatch(saveBookingError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(saveBookingError(error.message));


            }

        });
    };

}
