import {

    
    LOG_IN_START,
    LOG_IN_SUCCESS,
    LOG_IN_ERROR,

    SIGN_UP_START,
    SIGN_UP_SUCCESS,
    SIGN_UP_ERROR,

    LOG_OUT_START,
    LOG_OUT_SUCCESS,
    LOG_OUT_ERROR,

    VALIDATE_TOKEN_START,
    VALIDATE_TOKEN_SUCCESS,
    VALIDATE_TOKEN_ERROR,
    
    CHANGE_PASSWORD_START,
    CHANGE_PASSWORD_SUCCESS,
    CHANGE_PASSWORD_ERROR,
    
    FORGOT_PASSWORD_START,
    FORGOT_PASSWORD_SUCCESS,
    FORGOT_PASSWORD_ERROR,

} from './ActionTypes';

export const loginStart = (email, password) => (
    {
        type: LOG_IN_START,
        email: email,
        password: password,

    }
)

export const loginSuccess = (customer, token) => (
    {
        type: LOG_IN_SUCCESS,
        customer: customer,
        token: token

    }
)


export const loginError = (errorMsg) => (
    {
        type: LOG_IN_ERROR,
        error: errorMsg

    }
)

