import {

    makePaymentStart,
    makePaymentSuccess,
    makePaymentError,
} from '../actions/PaymentActions';

import URLS from '../../config/urls';
import API_KEYS from '../../config/apiKeys';
import axios from 'axios'


// import * as AxiosLogger from 'axios-logger';




// REMOVE FROM PROD
// axios.interceptors.request.use(AxiosLogger.requestLogger);
// axios.interceptors.response.use(AxiosLogger.responseLogger);

export const makePaymentMiddleware = (bookingId, customerName, customerEmail, amount) => {
    return function (dispatch) {

        dispatch(makePaymentStart());

        return axios.post(
            URLS.MAKE_PAYMENT_URL,
            {
                emailOrMobileNumber: API_KEYS.slydePayEmail,
                merchantKey: API_KEYS.slydePayMerchantKey,
                amount: amount,
                orderCode: bookingId,
                description: "Invoice for your car rental",
                sendInvoice: true,
                payOption: "SLYDEPAY",
                customerName: customerName,
                customerEmail: customerEmail
            }


        ).then(function (response) {

            /* 
                {
                    "success": true,
                    "result": {
                        "orderCode": "25261053e6d411ea",
                        "paymentCode": "jtqnb8",
                        "payToken": "0c8227d9-ffd6-4e5e-b19f-ecbc19557f35",
                        "description": "Invoice for your car rental",
                        "qrCodeUrl": "https://app.slydepay.com/download/qrcodes/jtqnb8.png",
                        "fullDiscountAmount": 0.0,
                        "discounts": [],
                        "externalTransRef": null
                    },
                    "errorMessage": "",
                    "errorCode": null
                }
            */


            if (response.data.success) {

                dispatch(makePaymentSuccess(
                    response.data.result.payToken,
                    response.data.result.paymentCode,
                    response.data.result.qrCodeUrl)
                )



            } else {

                dispatch(makePaymentError(response.data.errorMessage+" Error Code:"+response.data.errorCode));
            }


        }).catch(function (error) {

            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(makePaymentError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(makePaymentError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(makePaymentError(error.message));


            }

        });
    };

}
