import {
  
  GET_PROFILE_START,
  GET_PROFILE_SUCCESS,
  GET_PROFILE_ERROR,

  UPDATE_LICENSE_START,
  UPDATE_LICENSE_SUCCESS,
  UPDATE_LICENSE_ERROR,
  
} from '../actions/ActionTypes';

const INITIAL_STATE = {
  ACTION_TYPE :"",
  isGetProfileLoading: false,
  isUpdateLicenseLoading: false,
  
  
  getProfileError: "",
  updateLicenseError:"",
  

  customer:{
    /*
     "id": "29e21100d13411ea",
    "ctype": "Individual",
    "firstname": "New",
    "lastname": "Customer",
    "email": "me@yahoo.com",
    "phone": "0206584659",
    "country": "",
    "idtype": "",
    "idnumber": "",
    "license": "",
    "licensepic": "",
    "confirm": "0",
    "confirm_code": ""

    */
  },
  

}


const ProfileReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };

  newState.ACTION_TYPE = action.type;

  switch (action.type) {

    
    case GET_PROFILE_START:

      newState.isGetProfileLoading = true;
      newState.getProfileError=""
    //   newState.customer={};
  
      

      return newState;

    case GET_PROFILE_SUCCESS:

      newState.isGetProfileLoading = false;
      newState.getProfileError="";
      newState.customer= action.customer;
      
      return newState;

    case GET_PROFILE_ERROR:    
            
      
      newState.isGetProfileLoading = false;
      newState.getProfileError= action.error;
            
      return newState;
    
    case UPDATE_LICENSE_START:

      newState.isUpdateLicenseLoading = true;
      newState.updateLicenseError=""
      
      
     
      

      return newState;

    case UPDATE_LICENSE_SUCCESS:

      newState.isUpdateLicenseLoading = false;
      newState.updateLicenseError=action.error;
      // newState.customer= action.data;
      
      return newState;

    case UPDATE_LICENSE_ERROR:    
            
      
      newState.isUpdateLicenseLoading = false;
      newState.updateLicenseError= action.error;
            
      return newState;


    default:
      return newState;


  }
};
export default ProfileReducer;