import React, { useState } from "react";
import {
  Button, Col,
  Modal,
  ModalBody, ModalHeader, Row,
  Table,UncontrolledCarousel 
} from "reactstrap";
import { colors } from "../styles/styles";

export default function AvailableVehicles({  item ,onVehicleSelected ,carImages }) {
  const [detailsModal, setDetailsModal] = useState(false);

  const toggleDetails = () => setDetailsModal(!detailsModal);


  return (
    <>
      <div
        class="col-md-6 col-sm-8"
        style={{
          ...vehiclesStyle.itemWrapper,
          ...{
            backgroundImage: "url(" + item.pics[0] + ")",
          },
        }}
      >
        <Row style={vehiclesStyle.background}>
          <div style={vehiclesStyle.carName}>{item.vehicle}</div>
          <div style={vehiclesStyle.available}>Available: {item.available}</div>
          <div style={vehiclesStyle.row}>
            <Button style={vehiclesStyle.button} onClick={toggleDetails}>
              Details
            </Button>
            <Button
              style={vehiclesStyle.button}
              onClick={ ()=>onVehicleSelected(item)}
            >
              Select
            </Button>
          </div>
        </Row>
      </div>

      {/* modal */}
      <Modal
        isOpen={detailsModal}
        toggle={toggleDetails}
        className={"col-12"}
        size="xl"
      >
        <ModalHeader toggle={toggleDetails}>{item.vehicle} </ModalHeader>
        <ModalBody>
          <Row>
            <Col lg="7" sm="12" style={modalStyle.carousel}>
              <Row>
                <UncontrolledCarousel items={carImages} />
              </Row>
            </Col>
            <Col>
              <Table borderless>
                <tbody>
                  <tr>
                    <td>Cost</td>
                    <td>GHC {item.price.total}</td>
                  </tr>
                  <tr>
                    <td>Gearbox</td>
                    <td>{item.gearbox}</td>
                  </tr>
                  <tr>
                    <td>Suticases</td>
                    <td>{item.suitcases}</td>
                  </tr>
                  <tr>
                    <td>No. of doors</td>
                    <td>{item.doors}</td>
                  </tr>
                  <tr>
                    <td>Passengers</td>
                    <td>{item.passengers}</td>
                  </tr>
                  <tr>
                    <td>Car Description</td>
                    <td>{item.description}</td>
                  </tr>
                </tbody>
              </Table>
            </Col>
          </Row>
        </ModalBody>
      </Modal>
    </>
  );
}

const vehiclesStyle = {
  itemWrapper: {
    marginBottom: 20,
    height: 250,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    borderStyle: "solid",
    borderColor: colors.bg,
    borderWidth: 1,
    // borderRadius: 10,
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-end",
  },

  background: {
    padding: 10,
    height: "100%",
    backgroundColor: "rgba(0,0,0,.4)",
    justifyContent: "space-around",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },

  carName: {
    color: "white",
    fontSize: 20,
    textAlign: "center",
  },

  available: {
    fontSize: 18,
    color: "white",
  },

  button: {
    backgroundColor: colors.gold,
    fontSize: 13,
  },

  row: {
    // borderStyle: "solid",
    display: "flex",
    width: "100%",
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
  },
};

const modalStyle = {
  carousel: {
    // borderStyle: "solid",
    height: 400,
  },
};
