import React from "react";
import {
    Redirect, Route, Switch
} from "react-router-dom";
import GuestNavBarComponent from '../components/GuestNavBarComponent';
import AvailableCarsPage from '../views/user/AvailableCarsPage';
import BookARidePage from '../views/user/BookARidePage';
import ReservationSummaryPage from '../views/user/ReservationSummaryPage';
import MyBookingSummaryPage from "../views/user/MyBookingSummaryPage";



export default function GuestLayout() {
    return (
        <div>
            <GuestNavBarComponent/>

            <Switch>

                <Route path="/guest/book" render={(props)=><BookARidePage {...props} isGuest={true}/>} />
                <Route path="/guest/available-cars" render={ props=><AvailableCarsPage {...props} isGuest={true}/>} />
                <Route path="/guest/reservation-summary" render={props =><ReservationSummaryPage {...props} isGuest={true}/>} />
                <Route path="/guest/my-booking-summary/:bookingId" render={props =><MyBookingSummaryPage {...props} isGuest={true}/>} />
                <Redirect to="/guest/book" />

            </Switch>
        </div>
    );
}

