import {

    getProfileStart,
    getProfileSuccess,
    getProfileError,
    updateLicenseStart,
    updateLicenseError,
    updateLicenseSuccess

} from '../actions/ProfileActions';

import URLS from '../../config/urls';
import axios from 'axios'


// import * as AxiosLogger from 'axios-logger';




//REMOVE FROM PROD
// axios.interceptors.request.use(AxiosLogger.requestLogger);
// axios.interceptors.response.use(AxiosLogger.responseLogger);

export const getProfileMiddleware = (customerId) => {
    return function (dispatch) {


        //console.log("Customer id: "+customerId);

        dispatch(getProfileStart());

        return axios.post(
            URLS.GET_PROFILE_URL,

            { id: customerId }

        ).then(function (response) {


            if (response.data.status === 'success') {

               dispatch( getProfileSuccess( response.data.customer))



            } else {

                dispatch(getProfileError( response.data.message ));
            }


        }).catch(function (error) {

            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(getProfileError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(getProfileError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(getProfileError(error.message));


            }

        });
    };

}


