import React from "react";
import {
  Button,
  Form,
  Input,
  Container,
  Col,
  Row,
  Spinner,
} from "reactstrap";

import { Link } from "react-router-dom";
import car from "../../assets/brand/car2.svg";
import { colors } from "../../styles/styles";

//redux imports
import { useDispatch, useSelector } from "react-redux";
import { SIGN_UP_ERROR, SIGN_UP_SUCCESS } from "../../redux/actions/ActionTypes";
// import { signUpError } from "../../redux/actions/AuthenticateActions";
// import { goToLoginScreen } from "../../redux/actions/NavigationActions";
import { signUpMiddleware } from "../../redux/middleware/AuthenticateMiddleware";

function SignUpPage(props) {
  //redux
  const dispatch = useDispatch();
  const authenticateState = useSelector((state) => state.authenticate);

  const [firstName, setfirstName] = React.useState("");
  const [lastName, setLastName] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [confirmPassword, setConfirmPassword] = React.useState("");
  const [individual, setIndividual] = React.useState(true);
  const [corporate, setCorporate] = React.useState(false);
  const [accountType, setAccountType] = React.useState("Individual");
  const [checkBox, setCheckBox] = React.useState(false);

  React.useEffect(() => {
    console.log(authenticateState.ACTION_TYPE);
    if (authenticateState.ACTION_TYPE === SIGN_UP_SUCCESS) {
      alert(authenticateState.signUpError)
    }
  }, [authenticateState.ACTION_TYPE]);

  const validateFields = () => {
    if (
      firstName === "" ||
      lastName === "" ||
      phone === "" ||
      email === "" ||
      password === "" ||
      confirmPassword === ""
    ) {
      alert("Fields cannot be empty");
      return;

      //check for email validity
    } else if (
      email.match(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/) === null
    ) {
      alert("Please enter a valid email address");
      return;
    } else if (password !== confirmPassword) {
      alert("Passwords do not match");
      return;
    } else if (password.length < 5) {
      alert("Password cannot be less than 5");
      return;
    } else if (checkBox == false) {
      alert("Please agree to the Terms and Conditions");
      return;
    }

    dispatch(
      signUpMiddleware(firstName, lastName, accountType, email, password, phone)
    );
  };

  React.useEffect(() => {
    if (authenticateState.ACTION_TYPE === SIGN_UP_SUCCESS) {
        alert(authenticateState.signUpError)
        props.history.push("/user");

    } else if (authenticateState.ACTION_TYPE === SIGN_UP_ERROR) {

        alert(authenticateState.signUpError);

    }

}, [authenticateState.ACTION_TYPE]);
  return (
    <Container fluid>
      <Row>
        <Col xs="12" md="4" sm="6" style={leftStyle}>
          <Form
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <Input
              value={firstName}
              onChange={(e) => setfirstName(e.target.value)}
              type="text"
              placeholder="First Name"
              style={inputStyle} />

            <Input
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
              type="text"
              placeholder="Last Name"
              style={inputStyle} />

            <Input
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              type="tel"
              placeholder="Telephone Number"
              style={inputStyle}
            />
            <Input
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              type="text"
              placeholder="Email"
              type="email"
              style={inputStyle}
            />
            <Input
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              type="password"
              placeholder="Password"
              style={inputStyle} />
            <Input
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              type="password"
              placeholder="Confirm Password"
              style={inputStyle}
            />
            <p
              style={{
                fontSize: 20,
                alignSelf: "flex-start",
                margin: 0,
              }}
            >
              Account type
            </p>
            <Col>
              <input
                checked={individual}
                onChange={() => {
                  

                  setIndividual(true);
                  setCorporate(false);
                  setAccountType("Individual");

                }}
                type="radio"
                value="Male"
                // name="gender"
              />
              {"  "}
              Individual{" "}
              <input
              checked={corporate}
                onChange={() => {
                  setIndividual(false);
                  setCorporate(true);
                  setAccountType("Corporate");
                }}
                type="radio"
                value="Male"
                // name="gender"
                style={{ paddingHorizontal: 20 }}
              />{" "}
              Corporate
            </Col>

            <Col style={{ width: "100%", marginTop: 10 }}>
              <Input checked={checkBox} onChange={()=>setCheckBox(!checkBox)} type="checkbox" />
              {"I agree to the "}
              <a
                style={{ color: colors.gold }}
                href="http://yoksghana.net:3000/terms-cond"
              >
                Terms and Conditions
              </a>
            </Col>

            <Button
              onClick={validateFields}
              style={buttonStyle}
            >
              {(authenticateState.isSignUpLoading ) ?  <Spinner color="light" /> : 'Sign Up'}
            </Button>
          </Form>

          <p>
            Already a user?{" "}
            <Link to="/auth/login" style={{ color: colors.gold }}>
              Login
            </Link>
          </p>
        </Col>
        <Col xs="auto" md="8" sm="6" style={rightStyle}>
          <img
            src={car}
            alt="Waradrive"
            width="80%"
            style={{ marginBottom: 50 }}
          />
        </Col>

      </Row>
    </Container>
  );
}

const leftStyle = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  // borderStyle: "solid",
  height: "100vh",
  width: "100vw",
};

const rightStyle = {
  backgroundColor: colors.bg,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  height: "100vh",
  width: "100%",
};

const inputStyle = {
  width: "100%",
  backgroundColor: colors.bg,
  borderRadius: 100,
  height: 40,
  borderStyle: "none",
  marginBottom: 20,
  textAlign: "left",
  paddingLeft: 25,
  alignSelf: "center",
};

const buttonStyle = {
  width: "100%",
  backgroundColor: colors.dark,
  borderRadius: 100,
  height: "40",
  borderStyle: "none",
  marginBottom: 10,
  marginTop: 20,
};

export default SignUpPage;
