import {

    MAKE_PAYMENT_START,
    MAKE_PAYMENT_SUCCESS,
    MAKE_PAYMENT_ERROR,
    

} from './ActionTypes';


export const makePaymentStart = () => (
    {
        type: MAKE_PAYMENT_START  ,           
    }
)
export const makePaymentSuccess = ( payToken,paymentCode, qrCodeUrl) => (
    {
        type: MAKE_PAYMENT_SUCCESS  , 
        payToken:payToken,      
        paymentCode: paymentCode    ,
        qrCodeUrl: qrCodeUrl    ,
    }
)
export const makePaymentError = (errorMsg) => (
    {
        type: MAKE_PAYMENT_ERROR  , 
        error: errorMsg          
    }
)
