import {
  
  LOG_IN_START,
  LOG_IN_SUCCESS,
  LOG_IN_ERROR,

  SIGN_UP_START,
  SIGN_UP_SUCCESS,
  SIGN_UP_ERROR,
  LOG_OUT_START,
  LOG_OUT_SUCCESS,
  LOG_OUT_ERROR,

  VALIDATE_TOKEN_START,
  VALIDATE_TOKEN_SUCCESS,
  VALIDATE_TOKEN_ERROR,

  CHANGE_PASSWORD_START,
  CHANGE_PASSWORD_SUCCESS,
  CHANGE_PASSWORD_ERROR,
  
  FORGOT_PASSWORD_START,
  FORGOT_PASSWORD_SUCCESS,
  FORGOT_PASSWORD_ERROR,
 

} from '../actions/ActionTypes';

const INITIAL_STATE = {
  ACTION_TYPE :"",

  isLoginLoading: false,
  isSignUpLoading: false,
  isLogOutLoading: false,
  isChangePasswordLoading: false,
  isForgotPasswordLoading: false,

  loginError: "",
  signUpError:"",
  logOutError:"",
  changePasswordError: "",
  forgotPasswordError: "",

  customer_id: "",
  token:"",
  customer:{
    /*
     "id": "29e21100d13411ea",
    "ctype": "Individual",
    "firstname": "New",
    "lastname": "Customer",
    "email": "me@yahoo.com",
    "phone": "0206584659",
    "country": "",
    "idtype": "",
    "idnumber": "",
    "license": "",
    "licensepic": "",
    "confirm": "0",
    "confirm_code": ""

    */
  },
  

}


const AuthenticateReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };
  newState.ACTION_TYPE = action.type;


  switch (action.type) {

    case LOG_IN_START:
      newState.isLoginLoading = true;
      newState.loginError=""
      newState.customer={};
  
      return newState;

    case LOG_IN_SUCCESS: case VALIDATE_TOKEN_SUCCESS:
      newState.isLoginLoading = false;
      newState.loginError="";
      newState.customer= action.customer;
      newState.customer_id = action.customer.id;
      newState.token = action.token;
      
      return newState;

    case LOG_IN_ERROR: case VALIDATE_TOKEN_ERROR:
      newState.isLoginLoading = false;
      newState.loginError= action.error;
            
      return newState;
    
    case SIGN_UP_START:
      newState.isSignUpLoading = true;
      newState.signUpError=""
      // newState.customer={}
      
      return newState;

    case SIGN_UP_SUCCESS:
      newState.isSignUpLoading = false;
      newState.signUpError=action.error;
      
      return newState;

    case SIGN_UP_ERROR:    
      newState.isSignUpLoading = false;
      newState.signUpError= action.error;
            
      return newState;

    case LOG_OUT_START:                      
      newState.isLogOutLoading = true;                  
      return newState;

    case LOG_OUT_SUCCESS:                      
      newState.isLogOutLoading = false;                  
      return newState;

    case LOG_OUT_ERROR:                      
      newState.isLogOutLoading = false;                  
      return newState;
    
    case CHANGE_PASSWORD_START:
        newState.isChangePasswordLoading = true;
  
        return newState;
  
    case CHANGE_PASSWORD_SUCCESS:
        newState.isChangePasswordLoading = false;
        newState.changePasswordError = action.error;
  
        return newState;
  
    case CHANGE_PASSWORD_ERROR:
        newState.isChangePasswordLoading = false;
        newState.changePasswordError = action.error;
  
        return newState;
  
    case FORGOT_PASSWORD_START:
        newState.isForgotPasswordLoading = true;
        return newState;
  
    case FORGOT_PASSWORD_SUCCESS:
        newState.isForgotPasswordLoading = false;
        newState.forgotPasswordError = action.error;
  
        return newState;
  
    case FORGOT_PASSWORD_ERROR:
        newState.isForgotPasswordLoading = false;
        newState.forgotPasswordError = action.error;
  
        return newState;

    default:
      return newState;


  }
};
export default AuthenticateReducer;