import React from "react";
import FullHeight from "react-full-height";
import { IconContext } from "react-icons";
import { FaBook, FaHome, FaSignOutAlt, FaUser } from "react-icons/fa";
import { Link } from "react-router-dom";
import {
  Col, Row
} from "reactstrap";
import logo from "../assets/brand/logo1.svg";
import { colors } from "../styles/styles";

import { useDispatch, useSelector } from "react-redux";
// import { logOutMiddleware } from '../redux/middleware/AuthenticateMiddleware';

export default function UserSideBarComponent(props) {
  //redux
  const dispatch = useDispatch();
  const authenticateState = useSelector(state => state.authenticate);
  return (
    <Row>
      {/* this full height is a resposive that take the full height for 
            all browsers. all heavy lifting done */}
      <FullHeight canExceed style={{ width: '100%' }}>
        {/* logo start */}
        <Col style={logoContainer}>
          <Link style={menuText} to="/user">
            <img
              src={logo}
              alt="logo"
              style={{ width: 75, height: 150, resizeMode: "contain" }}
            />
          </Link>
        </Col>
        {/* logo end */}
        <div
          class="col-12"
          style={{
            backgroundColor: colors.bg,
            // borderStyle: "solid",
          }}
        >
          {/* user header start */}
          <Row style={userHeader}>
            <div style={licenseCard}></div>
            <h5 style={{ margin: 0, marginTop: 10, fontSize: 18 }}>
              {authenticateState.customer.firstname + " " + authenticateState.customer.lastname}
            </h5>
            <h6
              style={{
                margin: 0,
                fontSize: "15",
                fontWeight: "300",
              }}
            >
              {authenticateState.customer.email}
            </h6>
          </Row>
          {/* user header end */}

          {/* menu start */}
          <Row style={menu}>
            <IconContext.Provider
              value={{ style: { fontSize: 16, color: colors.dark } }}
            >
              <FaHome />
            </IconContext.Provider>
            <Link style={menuText} to="/user/my-bookings">
              Bookings
            </Link>
          </Row>
          <Row style={menu}>
            <IconContext.Provider
              value={{ style: { fontSize: 16, color: colors.dark } }}
            >
              <FaBook />
            </IconContext.Provider>
            <Link style={menuText} to="/user/book">
              Book a ride
            </Link>
          </Row>
          <Row style={menu}>
            <IconContext.Provider
              value={{ style: { fontSize: 16, color: colors.dark } }}
            >
              <FaUser />
            </IconContext.Provider>
            <Link style={menuText} to="/user/profile">
              Profile
            </Link>
          </Row>
          {/* menu end */}

          {/* logout start */}
          <div style={logoutStyle}>
            <Row style={menu}>
              <IconContext.Provider
                value={{ style: { fontSize: 16, color: colors.dark } }}
              >
                <FaSignOutAlt />
              </IconContext.Provider>
              <Link style={menuText} to="/auth">
                Sign Out
              </Link>
            </Row>
          </div>
          {/* logout end */}
        </div>
      </FullHeight>
    </Row>
  );
}

const logoContainer = {
  backgroundColor: colors.header,
  height: 60,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};

const userHeader = {
  borderStyle: "solid",
  height: "35vh",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  borderBottomWidth: 0.1,
  borderTopWidth: 0,
  borderLeftWidth: 0,
  borderRightWidth: 0,
  borderColor: "#c4c4c4",
  marginBottom: 20,
};

const licenseCard = {
  backgroundImage:
    "url(" +
    "https://images.pexels.com/photos/34153/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=350" +
    ")",
  backgroundPosition: "center",
  backgroundSize: "cover",
  backgroundRepeat: "no-repeat",
  height: "20vh",
  width: "20vw",
  borderStyle: "solid",
  borderRadius: 7,
};

const menu = {
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
  //   justifyContent: "center",
  paddingLeft: 40,
  paddingRight: 20,
  paddingTop: 10,
  paddingBottom: 10,
  //   borderStyle:'solid',
};

const menuText = {
  margin: 0,
  paddingLeft: 10,
  color: colors.dark,
  fontSize: 16,
  fontWeight: "500",
};

const logoutStyle = {
  // borderStyle:'solid',
  height: "20vh",
  display: "flex",
  flexDirection: "column",
  justifyContent: "flex-end",
};
