//keep track of ids of the reservation types for conditional rendering of required/non required fields
const HOURLY = "1";
const WHOLE_DAY_IN_ACCRA = "2";
const WHOLE_DAY_OUTSIDE_ACCRA = "3";
const FINANCE_LEASE = "4";
const TAXI_SERVICE = "5";

export const RESERVATION_TYPES = {

    HOURLY,
    WHOLE_DAY_IN_ACCRA,
    WHOLE_DAY_OUTSIDE_ACCRA,
    FINANCE_LEASE,
    TAXI_SERVICE
}

export const getReservationTypeById= (reservationId)=>{

    let name ="UNKNOWN RESERVATION TYPE"
    switch (reservationId) {
        case HOURLY:
            return "Hourly"
        case WHOLE_DAY_IN_ACCRA:
            return "Whole Day (Accra)"
        case WHOLE_DAY_OUTSIDE_ACCRA:
            return "Whole Day (Outside Accra)"

        case FINANCE_LEASE:
            return "Finance Lease"
            
        case TAXI_SERVICE:
            return "Taxi Service"
        
    
        default:
            return "UNKNOWN RESERVATION TYPE"
    }

}