import React from 'react';
import {
  Table
} from "reactstrap";
import { getReservationTypeById } from '../constants/reservation';

export  function ReservationTable({item}) {
    return (
      <>
        <Table striped borderless style={table}>
          <tbody>
            <tr>
              <td style={leftStyle}>Reservation Type</td>
              <td style={rightStyle}>{getReservationTypeById( item.booking.rtype)}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Service Type</td>
              <td style={rightStyle}>{item.booking.stype}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Started Date and Time</td>
              <td style={rightStyle}>{item.booking.starttime}</td>
            </tr>
            <tr>
              <td style={leftStyle}>End Date and Time</td>
              <td style={rightStyle}>{item.booking.endtime}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Pickup Location</td>
              <td style={rightStyle}>{item.booking.pickup}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Drop-off Location</td>
              <td style={rightStyle}>{item.booking.dropoff}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Distance</td>
              <td style={rightStyle}>{item.booking.distance}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Hours</td>
              <td style={rightStyle}>{item.booking.hours}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Comment</td>
              <td style={rightStyle}>{item.booking.comments}</td>
            </tr>
            <tr>
              <td style={leftStyle}>Selected Car</td>
              <td style={rightStyle}>{item.cardetails.vehicle}</td>
            </tr>
          </tbody>
        </Table>
      </>
    );
}

export  function PaymentTable({ item }) {
  return (
    <>
      <Table striped borderless style={table}>
        <tbody>
          <tr>
            <td style={leftStyle}>Base Rate</td>
            <td style={rightStyle}>
              GH₵ {item.cardetails.base_rate}
            </td>
          </tr>
          
          <tr>
            <td style={leftStyle}>
              Cost (Base Rate without Tax * Number of Hours/days)
            </td>
            <td style={rightStyle}>GH₵ {item.cardetails.rate}</td>
          </tr>
          <tr>
            <td style={leftStyle}>NHIL ({item.cardetails.nhil_value}%)</td>
            <td style={rightStyle}>GH₵ {item.cardetails.nhil}</td>
          </tr>
          <tr>
            <td style={leftStyle}>GetFund ({item.cardetails.getfund_value}%)</td>
            <td style={rightStyle}>GH₵ {item.cardetails.getfund}</td>
          </tr>
          <tr style={total}>
            <td style={leftStyle}>Total Cost (Cost+Nhil+GetFund)</td>
            <td style={rightStyle}>GH₵ {item.cardetails.rate}</td>
          </tr>
          <tr>
            <td style={leftStyle}>VAT({item.cardetails.vat_value}% OF Total Cost)</td>
            <td style={rightStyle}>GH₵ {item.cardetails.vat}</td>
          </tr>

          <tr>
            <td style={leftStyle}>Tourism ({item.cardetails.tourism_value}% of Total Cost)</td>
            <td style={rightStyle}>GH₵ {item.cardetails.tourism}</td>
          </tr>
          <tr>
            <td style={leftStyle}>Driver's Allowance (Base rate per day)</td>
            <td style={rightStyle}>
              GH₵ {item.cardetails.base_driver_allowance}
            </td>
          </tr>
          <tr>
            <td style={leftStyle}>
              Driver's Allowance (Base rate * Number of days)
            </td>
            <td style={rightStyle}>
              GH₵ {item.cardetails.driver_allowance}
            </td>
          </tr>
          <tr style={totalCost.wrapper}>
            <td style={totalCost.leftStyle}>Total Cost</td>
            <td style={totalCost.rightStyle}>GH₵ {item.cardetails.total}</td>
          </tr>
        </tbody>
      </Table>
    </>
  );
}


const rightStyle={
    textAlign:'right',
    fontSize:13.5
}

const leftStyle = {
  textAlign: "left",
  fontSize: 13.5,
};

const total={
    borderStyle: "solid",
    borderRightWidth:0,
    borderLeftWidth: 0,
    borderTopWidth:0,
    borderBottomWidth: 1,
    borderColor:'grey',
}

const totalCost = {
  wrapper: {
    borderStyle: "solid",
    borderRightWidth: 0,
    borderLeftWidth: 0,
    borderTopWidth: 1,
    borderBottomWidth: 1,
    borderColor: "grey",
  },
  leftStyle: {
    textAlign: "left",
    fontSize: 20,
  },
  rightStyle: {
    textAlign: "right",
    fontSize: 20,
    width: '50%'
  },
};

const table = { borderStyle: "solid", borderWidth: 0.5, borderColor: "#f1F1F1" };