import {
  GO_TO_LOGIN,
  GO_TO_SIGNUP,
  GO_TO_HOME,
  GO_TO_LOADING,

} from '../actions/ActionTypes';

const INITIAL_STATE = {
  isLoading: false,
  isLoggedIn: false,
  isLoggedOut: false,
  isNewUser: false,

}


const NavigationReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };


  switch (action.type) {

    
    case GO_TO_LOADING:

      newState.isLoading = true;
      newState.isLoggedIn = false;
      newState.isLoggedOut = false;
      newState.isNewUser = false;

      return newState;

    case GO_TO_LOGIN:

      newState.isLoading = false;
      newState.isLoggedIn = false;
      newState.isLoggedOut = false;
      newState.isNewUser = false;
      return newState;

    case GO_TO_SIGNUP:
      
      newState.isLoading = false;
      newState.isLoggedIn = false;
      newState.isLoggedOut = false;
      newState.isNewUser = true;
    

      return newState;

    case GO_TO_HOME:    
            
      newState.isLoggedIn = true;      
      newState.isLoading = false;
      newState.isLoggedOut = false;
      newState.isNewUser = false;
            
      return newState;

    

    default:
      return newState;


  }
};
export default NavigationReducer;