import React, { useState } from "react";
import {
    Collapse,
    Nav, Navbar,
    NavbarBrand, NavbarToggler,
    NavItem,
    NavLink
} from 'reactstrap';
import { colors } from '../styles/styles.js';

export default function UserNavBarComponent(props) {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);
    return (
        <>
            <Navbar  dark expand="md" style={{ backgroundColor: colors.header }}>
                <NavbarBrand href="/user">WARA DRIVE</NavbarBrand>
                <NavbarToggler onClick={toggle} />
                <Collapse isOpen={isOpen} navbar>
                    <Nav  navbar>
                        <NavItem>
                            <NavLink href="/user/my-bookings">My Bookings</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href="/user/book">Book a ride</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href="/user/profile">Profile</NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href="/auth">Logout</NavLink>
                        </NavItem>
                        <NavItem className="ml-auto">
                            <NavLink href=""></NavLink>
                        </NavItem>




                    </Nav>


                </Collapse>
            </Navbar>
        </>
    );
}
