import React from "react";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
    Button,
    Col, Container, Form,
    Input,
    Row,
    Spinner
} from "reactstrap";
import appstore from "../../assets/brand/appstore.svg";
import car from "../../assets/brand/car1.svg";
import playstore from "../../assets/brand/playstore.svg";
import waradrive from "../../assets/brand/waradrive.svg";
import { LOG_IN_ERROR, LOG_IN_SUCCESS } from "../../redux/actions/ActionTypes";
import { logInMiddleware } from "../../redux/middleware/AuthenticateMiddleware";
import { colors } from "../../styles/styles";



function LoginPage(props) {
    //redux
    const dispatch = useDispatch();
    const authenticateState = useSelector((state) => state.authenticate);

    const [email, setEmail] = React.useState("");
    const [password, setPassword] = React.useState("");

    const validateFields = () => {
        if (email === "" || password === "") {
            alert("Fields cannot be empty");
            return;
            //check for email validity
        } else if (
            email.match(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/) === null
        ) {
            alert("Please enter a valid email address");
            return;
        }

        dispatch(logInMiddleware(email, password));
    };

    const handleKeypress = e => {

        if (e.key === "Enter") {
            validateFields();
        }
    };

    React.useEffect(() => {
        if (authenticateState.ACTION_TYPE === LOG_IN_SUCCESS) {
            props.history.push("/user");

        } else if (authenticateState.ACTION_TYPE === LOG_IN_ERROR) {

            alert(authenticateState.loginError);

        }

    }, [authenticateState.ACTION_TYPE]);
    return (
        <Container fluid={true}>
            <Row>
                <Col xs="12" sm="6" md="4" style={leftStyle}>
                    <img
                        src={waradrive}
                        alt="Waradrive"
                        width="80%"
                        style={{ marginBottom: 50 }}
                    />
                    <Form style={{ width: "70%" }}>
                        <Input
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            onKeyPress={handleKeypress}
                            type="email"
                            placeholder="email"
                            style={inputStyle} />

                        <Input
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            onKeyPress={handleKeypress}
                            type="password"
                            placeholder="password"
                            style={inputStyle} />

                        <p class="text-center">
                            {" "}
                            <Link to="/auth/reset-password" style={linkStyle}>
                                Forgot Password
                            </Link>
                        </p>

                        <Button
                            onClick={validateFields}
                            style={buttonStyle}
                        >
                            {(authenticateState.isLoginLoading ) ?  <Spinner color="light" /> : 'Login'}
                        </Button>
                    </Form>

                    <p>
                        sign up?{" "}
                        <Link to="/auth/sign-up" style={linkStyle}>
                            Sign up
                        </Link>
                    </p>

                    <p>
                        <Link to="/guest" style={linkStyle}>
                            Book a ride as a guest
            </Link>
          </p>

          <Row style={appLink}>
            <a href="#">
              <img src={playstore} alt="playstore" />
            </a>
          <a href='#'>
                <img src={appstore} alt="appstore" />
          </a>
          </Row>
        </Col>
        <Col
          xs="auto"
          sm="6"
          md="8"
          style={rightStyle}
          class="d-none d-sm-block"
        >
          <img
            src={car}
            alt="Waradrive"
            width="80%"
            style={{ marginBottom: 50 }}
          />
        </Col>
      </Row>
    </Container>
  );
}

const leftStyle = {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    //   borderStyle: "solid",
    height: "100vh",
    width: "100%",
};

const rightStyle = {
    backgroundColor: colors.bg,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    width: "100%",
};

const inputStyle = {
    width: "100%",
    backgroundColor: colors.bg,
    borderRadius: 100,
    height: 40,
    borderStyle: "none",
    marginBottom: 20,
    textAlign: "center",
};

const buttonStyle = {
    width: "100%",
    backgroundColor: colors.dark,
    borderRadius: 100,
    height: 40,
    borderStyle: "none",
    marginBottom: 20,
};

const linkStyle = {
    color: colors.gold,
};

const appLink = {
    // borderStyle: "solid",
    width: "100%",
    justifyContent: 'space-around',
}

export default LoginPage;
