const BookingReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };

  newState.ACTION_TYPE = action.type;

  switch (action.type) {

    
    case GET_BOOKINGS_START:

      newState.isBookingsLoading = true;
      newState.getBookingsError=""

      return newState;

    case GET_BOOKINGS_SUCCESS:

      newState.isBookingsLoading = false;
      newState.getBookingsError="";
      newState.bookings= action.bookings;
      
      return newState;

    case GET_BOOKINGS_ERROR:    
                  
      newState.isBookingsLoading = false;
      newState.getBookingsError= action.error;
            
      return newState;
    
    case GET_BOOKING_START:

      newState.isBookingLoading = true;
      newState.getBookingError=""
     
      return newState;

    case GET_BOOKING_SUCCESS:

      newState.isBookingLoading = false;
      newState.getBookingError=action.error;
      newState.booking= action.booking;
      newState.cardetails= action.cardetails;
      
      return newState;

    case GET_BOOKING_ERROR:    
                  
      newState.isBookingLoading = false;
      newState.getBookingError= action.error;
            
      return newState;

      case SAVE_BOOKING_START:

      newState.isSaveBookingLoading = true;
      newState.saveBookingError=""
      newState.bookingId="";
     
      return newState;

    case SAVE_BOOKING_SUCCESS:

      newState.isSaveBookingLoading = false;
      newState.saveBookingError="BOOKING SUCCESSFULL...";
      newState.bookingId= action.bookingId;
     
      return newState;

    case SAVE_BOOKING_ERROR:    
                  
      newState.isSaveBookingLoading = false;
      newState.saveBookingError= action.error;
            
      return newState;

      case UPDATE_SAVE_BOOKING:
       

      if(action.firstname) newState.saveBooking.firstname = action.firstname;
      if(action.lastname) newState.saveBooking.lastname = action.lastname;
      if(action.email) newState.saveBooking.email = action.email;
      if(action.phone) newState.saveBooking.phone = action.phone;
      if(action.ctype) newState.saveBooking.ctype = action.ctype;
      if(action.customer) newState.saveBooking.customer = action.customer;
      if(action.rtype) newState.saveBooking.rtype = action.rtype;
      if(action.stype) newState.saveBooking.stype = action.stype;
      if(action.starttime) newState.saveBooking.starttime = action.starttime;
      if(action.endtime) newState.saveBooking.endtime = action.endtime;
      if(action.pickup) newState.saveBooking.pickup = action.pickup;
      if(action.pickupgps) newState.saveBooking.pickupgps = action.pickupgps;
      if(action.dropoff) newState.saveBooking.dropoff = action.dropoff;
      if(action.dropoffgps) newState.saveBooking.dropoffgps = action.dropoffgps;
      if(action.distance) newState.saveBooking.distance = action.distance;
      if(action.hours) newState.saveBooking.hours = action.hours;
      if(action.comments) newState.saveBooking.comments = action.comments;
      if(action.total_amt) newState.saveBooking.total_amt = action.total_amt;
      if(action.ccars) newState.saveBooking.ccars = action.ccars;
     

      console.log("------------------CURRENT SAVE BOOKING STATE------------------")
      console.log(newState.saveBooking);

      return newState;

      case RESET_SAVE_BOOKING:
        newState.saveBooking = {};
      
    default:
      return newState;


  }
};
export default BookingReducer;