import React from "react";
import {
  Redirect, Route, Switch
} from "react-router-dom";
import { Col, Container, Row } from "reactstrap";
import UserNavBarComponent from "../components/UserNavBarComponent";
import UserSideBarComponent from "../components/UserSideBarComponent";
import { colors } from "../styles/styles";
import AvailableCarsPage from "../views/user/AvailableCarsPage";
import BookARidePage from "../views/user/BookARidePage";
import MyBookingsPage from "../views/user/MyBookingsPage";
import MyBookingSummaryPage from "../views/user/MyBookingSummaryPage";
import ProfilePage from "../views/user/ProfilePage";
import ReservationSummaryPage from "../views/user/ReservationSummaryPage";


export default function UserLayout() {
  
  return (
    <>
      {/* This is the Nav Bar,it hides on large screens */}
      <div className="d-md-none">
        <UserNavBarComponent  />
      </div>

      <Container fluid>
        <Row>
          {/* Hide the side bar small screens */}
          <Col className="d-none d-md-block" md="3" style={sideBar}>
            <UserSideBarComponent />
          </Col>

          <Col md="9" style={content}>
            <Switch>
              <Route path="/user/my-bookings" component={MyBookingsPage} />
              <Route
                path="/user/my-booking-summary/:bookingId"
                component={MyBookingSummaryPage}
              />
              <Route path="/user/book" component={BookARidePage} />
              <Route
                path="/user/available-cars"
                component={AvailableCarsPage}
              />
              <Route
                path="/user/reservation-summary"
                component={ReservationSummaryPage}
              />
              <Route path="/user/profile" component={ProfilePage} />

              <Redirect to="/user/my-bookings" />
            </Switch>
          </Col>
        </Row>
      </Container>
    </>
  );
}

const sideBar = {
  backgroundColor: colors.bg,
  //   borderStyle: "solid",
};

const content = {
  backgroundColor: "white",
  //   borderStyle: "solid",
};
