import React from "react";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import {
  Col, Container,
  Row
} from "reactstrap";
import { PaymentTable, ReservationTable } from "../../components/BookingSummaryComponents";
// import LoadingScreen from './LoadingScreen';
// import ErrorScreen from './ErrorScreen';
import { GET_BOOKING_ERROR } from '../../redux/actions/ActionTypes';
import { getBookingMiddleware } from '../../redux/middleware/BookingMiddleware';



function MyBookingSummaryPage(props) {
  /* 2. Get the param */
  const { bookingId } = useParams();

  //redux
  const dispatch = useDispatch();
  const bookingState = useSelector(state => state.booking);

  React.useEffect(() => {
    if (bookingId) dispatch(getBookingMiddleware(bookingId));

  }, [])


  if (bookingState.isBookingLoading) {

    // return <LoadingScreen />
  }

  if (bookingState.ACTION_TYPE === GET_BOOKING_ERROR) {
    // return (
    //     <ErrorScreen onRetry={ () => dispatch(getBookingMiddleware(bookingId))} />
    // )
  }
  return (
    <Container fluid style={{ padding: 20 }}>
      <Row>
        <Col md="6" sm="12" style={wrapper}>
          <Row style={caption}>Reservation Summary</Row>

          <ReservationTable item={bookingState } />;
        </Col>
        <Col style={wrapper}>
          <Row style={caption}>Payment Summary</Row>
             <PaymentTable item={bookingState} />;
        </Col>
      </Row>
    </Container>
  );
}

const wrapper = {
  borderColor: "grey",
  borderWidth: 0.1,
};

const caption = {
  display: "flex",
  justifyContent: "center",
  padding: 10,
  fontSize: 20,
};


export default MyBookingSummaryPage;
