import React from "react";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Col, Container,
  CustomInput, Form,
  FormGroup,
  Input,
  Row, Spinner
} from "reactstrap";
import { GET_PROFILE_ERROR } from '../../redux/actions/ActionTypes';
// import LoadingScreen from './LoadingScreen';
// import ErrorScreen from './ErrorScreen';
import { changePasswordError } from '../../redux/actions/AuthenticateActions';
import { updateLicenseError } from '../../redux/actions/ProfileActions';
import { changePasswordMiddleware } from '../../redux/middleware/AuthenticateMiddleware';
import { getProfileMiddleware, updateLicenseMiddleware } from '../../redux/middleware/ProfileMiddleware';
import { colors } from "../../styles/styles";


function ProfilePage(props) {
  //redux
  const dispatch = useDispatch();
  const authenticateState = useSelector(state => state.authenticate);
  const profileState = useSelector(state => state.profile);


  const [oldPassword, setOldPassword] = React.useState("");
  const [newPassword, setNewPassword] = React.useState("");

  const [licenseNumber, setLicenseNumber] = React.useState("");
  const [licensePic, setLicensePic] = React.useState(null);

  const validateFields = () => {
    console.log(authenticateState.customer.password);
    dispatch(changePasswordError(""));
    if (oldPassword === "" || newPassword === "") {
      dispatch(changePasswordError("Fields cannot be empty"));
      return;
      //check for email validity
    } else if (oldPassword !== authenticateState.customer.password) {
      dispatch(changePasswordError("Passwords do not match"));
      return;
    }

    dispatch(changePasswordMiddleware(authenticateState.customer.email, newPassword));
  }

  const handleLicenseUpload = () => {


    if (licenseNumber === "") {
      dispatch(updateLicenseError("License number cannot be empty"))
      return;

    } else if (licensePic === null) {

      dispatch(updateLicenseError(" Please select your license image"))
      return;
    }

    // Create an object of formData 
    const formData = new FormData();


    // Update the formData object 
    formData.append(
      "id",
      profileState.customer.id,
    );
    formData.append(
      "license",
      licenseNumber,
    );
    formData.append(
      "licensepic",
      licensePic, 
      licensePic.name 

    );
    console.log(formData);

    dispatch(updateLicenseMiddleware(formData));

  }

  React.useEffect(() => {

    dispatch(getProfileMiddleware(authenticateState.customer_id));

  }, [])

  if (profileState.isGetProfileLoading) {

    // return <LoadingScreen />
  }

  if (profileState.ACTION_TYPE === GET_PROFILE_ERROR) {
    // return (
    //     <ErrorScreen onRetry={() => dispatch(getProfileMiddleware(authenticateState.customer_id))} />
    // )
  }
  return (
    <Container fluid style={container}>
      <Col xl="8" lg="8" md="10" style={content}>
        <Row style={{ ...header, ...flex }}>
          <h5 style={headerText}>Password Update</h5>
        </Row>

        <Row style={{ ...body, ...flex }}>
          <Form>
            <FormGroup>
              <Input
                value={oldPassword}
                onChange={(e) => setOldPassword(e.target.value)}
                type="password"
                placeholder="Enter old Password"
                style={inputStyle}
              />
            </FormGroup>
            <FormGroup>
              <Input
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                type="password"
                placeholder="Enter new Password"
                style={inputStyle}
              />
            </FormGroup>
            <div >{authenticateState.changePasswordError}</div>
            <Button onClick={validateFields} style={buttonStyle2}>
              {(authenticateState.isChangePasswordLoading) ? <Spinner color="light" /> : 'Update'}
            </Button>
          </Form>
        </Row>
        <Row style={{ ...header, ...flex }}>
          <h5 style={headerText}>Licence Update</h5>
        </Row>
        <Row style={{ ...body, ...flex }}>
          <Form style={{ display: "grid", placeItems: "center" }}>
            <FormGroup style={{ width: "70%" }}>
              <Input 
              value={licenseNumber}
              onChange={(e) => setLicenseNumber(e.target.value)}
              type="text" placeholder="License ID" style={inputStyle} />
            </FormGroup>
            <FormGroup style={{ width: "70%" }}>
              <CustomInput
                onChange={(e) => setLicensePic( e.target.files[0] )}
                type="file"
                id="exampleCustomFileBrowser"
                name="customFile"
              />
            </FormGroup>
            <div >{profileState.updateLicenseError}</div>
            <Button onClick={handleLicenseUpload} style={buttonStyle}>
              {(profileState.isUpdateLicenseLoading) ? <Spinner color="light" /> : 'Update'}
            </Button>
          </Form>
        </Row>
      </Col>
    </Container>
  );
}

const flex = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
};

const container = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  height: "100vh",
};

const content = {
  borderStyle: "solid",
  borderWidth: 1,
  borderColor: "#c4c4c4",
};

const header = {
  backgroundColor: colors.bg,
  padding: 10,
};

const headerText = {
  margin: 0,
  fontSize: "20",
  textAlign: "center",
};

const body = {
  padding: 20,
  width: "100%",
};

const inputStyle = {
  backgroundColor: colors.bg,
  borderRadius: 100,
  borderStyle: "none",
  marginBottom: 10,
  paddingLeft: 20,
};

const buttonStyle = {
  width: "70%",
  backgroundColor: colors.dark,
  borderRadius: 100,
  height: 40,
  borderStyle: "none",
  marginBottom: 10,
};

const buttonStyle2 = {
  width: "100%",
  backgroundColor: colors.dark,
  borderRadius: 100,
  height: 40,
  borderStyle: "none",
  marginBottom: 10,
};

export default ProfilePage;
