import {
  
  GET_SERVICES_START,
  GET_SERVICES_SUCCESS,
  GET_SERVICES_ERROR,

  
} from '../actions/ActionTypes';

const INITIAL_STATE = {
  isGetServicesLoading: false,
  getServicesError:"",

  services:[]
}


const ServiceReducer = (state = INITIAL_STATE, action) => {

  let newState = { ...state };


  switch (action.type) {

    
    case GET_SERVICES_START:

      newState.isGetServicesLoading = true;
      newState.getServicesError="";
    
      return newState;

    case GET_SERVICES_SUCCESS:

      newState.isGetServicesLoading = false;
      newState.getServicesError="";
      newState.services= action.services;
      
      return newState;

    case GET_SERVICES_ERROR:                
      
      newState.isGetServicesLoading = false;
      newState.getServicesError= action.error;
            
      return newState;
    
    default:
      return newState;


  }
};
export default ServiceReducer;