import React, { useState } from "react";
import FullHeight from "react-full-height";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Col, Row
} from "reactstrap";
import car from "../../assets/brand/null.svg";
import AvailableVehicles from "../../components/AvailableVehicleComponent";
// import LoadingScreen from './LoadingScreen';
// import ErrorScreen from './ErrorScreen';
import { GET_AVAILABLE_CARS_ERROR } from "../../redux/actions/ActionTypes";
import { updateSaveBooking } from '../../redux/actions/BookingActions';
import { getAvailableCarsMiddleware } from '../../redux/middleware/AvailableCarsMiddleware';
import { colors } from "../../styles/styles";


function AvailableCarsPage(props) {
  //this page will be used by both guest and authenticated user
  const routePath = (props.isGuest) ? "guest" : "user";
  // redux imports
  const dispatch = useDispatch();
  const bookingState = useSelector(state => state.booking);
  const availableCarsState = useSelector(state => state.availableCars);


  const [selectedCategory, setSelectedCategory] = useState('Select Category to see available cars');
  const [filteredVehicles, setFilteredVehicles] = React.useState([])
  React.useEffect(() => {

    dispatch(
      getAvailableCarsMiddleware(bookingState.saveBooking.rtype, bookingState.saveBooking.stype, bookingState.saveBooking.starttime, bookingState.saveBooking.endtime, bookingState.saveBooking.distance, bookingState.saveBooking.hours)
    );
  }, [])

  const filterVehiclesByCategory = (categoryItem) => {
    setSelectedCategory(categoryItem.name)
    let tempVehicles = availableCarsState.vehicles.filter(vehicle => vehicle.category_id === categoryItem.id);
    setFilteredVehicles(tempVehicles);

  }

  //when user clicks on a vehicle
  const onVehicleSelected = (vehicleItem) => {

    let vehicleObject = [
      {
        "car": vehicleItem.vehicle,
        "qty": 1,
        "price": vehicleItem.price
      }
    ]

    dispatch(
      updateSaveBooking(
        null, //customer id
        null, //reservation type
        null, //service type
        null, //start time
        null, //end time
        null, //pickup location
        null, //pick up gps
        null, //drop off location
        null, //dropoff gps
        null, //distace in km
        null, //hours
        null, //comments
        1, //total amount
        vehicleObject //selected car(s)
      ));

    props.history.push(`/${routePath}/reservation-summary`);

  }

  if (availableCarsState.isAvailableCarsLoading) {

    // return <LoadingScreen />
  }

  if (bookingState.ACTION_TYPE === GET_AVAILABLE_CARS_ERROR) {
    // return (
    //     <ErrorScreen onRetry={ () => dispatch(
    //       getAvailableCarsMiddleware(bookingState.saveBooking.rtype, bookingState.saveBooking.stype, bookingState.saveBooking.starttime, bookingState.saveBooking.endtime, bookingState.saveBooking.distance, bookingState.saveBooking.hours)
    //     )} />
    // )
  }
  return (
    <div style={content}>
      <Row>
        <Col md="4" style={categoryStyle.wrapper}>

          <Row style={categoryStyle.title}>Available Cars</Row>
          <Row style={categoryStyle.instruction}>
            Click on a category to see available vehicles
            </Row>
          {availableCarsState.categories.map((item) => {
            return (
              <Row key={item.id} style={categoryStyle.listWrapper}>
                <Button style={categoryStyle.button} onClick={() => filterVehiclesByCategory(item)}>{item.name}</Button>
              </Row>
            );
          })}

        </Col>
        <Col>
          <FullHeight canExceed className={"col-12"}>
            {filteredVehicles.length ? (
              <div>
                <Row style={vehiclesStyle.caption}>{selectedCategory}</Row>
                <Row style={vehiclesStyle.instruction}>
                  Click on details to see moe information about the vehicle before selecting it
                </Row>
                <Row
                  md="2"
                  sm="1"
                  style={{ display: "flex", justifyContent: "center" }}
                >
                  {filteredVehicles.map((item) => {
                    const image1 = item.pics.map(image => { return { src: image }; });
                    return <AvailableVehicles carImages={image1} key={item.vehicle} onVehicleSelected={onVehicleSelected} item={{ ...item }} />;
                  })}
                </Row>
              </div>
            ) : (
                <Col style={vehiclesStyle.null}>
                  <img src={car} alt="null" />
                  <h5>No vehicles available found</h5>
                  <p>select a category to see available vehicles</p>
                </Col>
              )}
          </FullHeight>
        </Col>
      </Row>
    </div>
  );
}

const content = {};

const categoryStyle = {
  wrapper: {
    borderStyle: "solid",
    borderColor: colors.bg,
  },
  title: {
    display: "flex",
    justifyContent: "center",
    textAlign: "center",
    fontSize: 20,
  },
  instruction: {
    fontSize: 12,
    paddingLeft: 10,
    paddingRight: 10,
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    color: "grey",
  },

  listWrapper: {
    display: "flex",
    justifyContent: "center",
    margin: 10,
  },

  button: {
    fontSize: 14,
    width: "100%",
  },
};

const vehiclesStyle = {
  null: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    paddingTop: "20vh",
  },

  instruction: {
    fontSize: 12,
    paddingBottom: 5,
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    color: "grey",
  },

  caption: {
    fontSize: 20,
    paddingTop: 5,
    textAlign: "center",
    display: "flex",
    justifyContent: "center"
  },
};

export default AvailableCarsPage;
