import polyline from "@mapbox/polyline";
import {
  GoogleMap,
  Marker,
  Polyline, useLoadScript
} from "@react-google-maps/api";
import _ from "lodash";
import React, { useState } from "react";
import FullHeight from "react-full-height";
//redux imports
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Col, Input, Label,
  Row, Spinner
} from "reactstrap";
import MapTextPredictionComponent from "../../components/MapTextPredictionComponent";
import apiKeys from "../../config/apiKeys";
import { RESERVATION_TYPES } from "../../constants/reservation";
import { updateSaveBooking } from "../../redux/actions/BookingActions";
import { colors, MapViewComponentStyle } from "../../styles/styles";


const initialRegion = {
  center: {
    lat: 5.55,
    lng: -0.1871,
  },
  zoom: 30,
  options: {
    styles: MapViewComponentStyle,
    disableDefaultUI: true,
    zoomControl: true,
  }
};

function BookARidePage(props) {

  //this page will be used by both guest and authenticated user
  const routePath = (props.isGuest)? "guest": "user";
  // redux imports
  const dispatch = useDispatch();
  const authenticateState = useSelector(state => state.authenticate);
  const bookingState = useSelector((state) => state.booking);

  //additional details hooks
  const [reservationType, setReservationType] = useState("");
  const [serviceType, setServiceType] = useState("Self-drive");
  const [pickUpDate, setPickUpDate] = useState(null);
  const [dropOffDate, setDropOffDate] = useState(null);
  const [pickUpTime, setPickUpTime] = useState(null);
  const [dropOffTime, setDropOffTime] = useState(null);
  const [comments, setComments] = useState(null);

  const [pickUpLocationState, setPickUpLocation] = useState(null);
  const [dropOffLocationState, setDropOffLocation] = useState(null);
  const [pickUpLocationCoords, setPickUpLocationCoords] = useState({});
  const [dropOffLocationCoords, setDropOffLocationCoords] = useState({});
  const [pickUpLocationPredictions, setPickUpLocationPredictions] = useState([]);
  const [dropOffLocationPredictions, setDropOffLocationPredictions] = useState([]);
  const [dropOffLocationDirections, setDropOffLocationDirections] = useState(null);
  const [travelDistance, setTravelDistance] = useState({ text: "", value: 0 });
  const [travelDuration, setTravelDuration] = useState({ text: "", value: 0 });

  //error message
  const [errorMessage, setErrorMessage] = useState("");


  //map hooks
  const _map = React.useRef();
  const onMapLoad = React.useCallback((map) => {
    _map.current = map;
  }, []);
  const panTo = React.useCallback(({ lat, lng }) => {
    //setCoordinates({ lat, lng })
    _map.current.panTo({ lat, lng });
    _map.current.setZoom(20);
  }, []);
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: apiKeys.google,
  });



  let pickUpLocationPlaceId = null;
  let dropOffLocationPlaceId = null;

  let pickUpLocationMarker = null;
  let dropOffLocationMarker = null;
  let dropOffLocationPolyline = null;

  const validateMapFields = () => {

    setErrorMessage('')

    switch (reservationType) {

      case RESERVATION_TYPES.TAXI_SERVICE:
        if (pickUpLocationState === null || dropOffLocationState === null || pickUpDate === null || pickUpTime === null) {
          setErrorMessage("Fields cannot be empty");
          return;
        }
        break;

      case RESERVATION_TYPES.HOURLY:

        if (pickUpLocationState === null || dropOffLocationState === null || pickUpDate === null || pickUpTime === null || dropOffDate === null || dropOffTime === null) {

          setErrorMessage("Fields cannot be empty");
          return;
        }
        break;

      case RESERVATION_TYPES.WHOLE_DAY_IN_ACCRA:

        if (pickUpLocationState === null  || pickUpDate === null || pickUpTime === null || dropOffDate === null || dropOffTime === null) {

          setErrorMessage("Fields cannot be empty");
          return;
        }
        break;
      case RESERVATION_TYPES.WHOLE_DAY_OUTSIDE_ACCRA:

        if (pickUpLocationState === null || dropOffLocationState === null || pickUpDate === null || pickUpTime === null || dropOffDate === null || dropOffTime === null) {

          setErrorMessage("Fields cannot be empty");
          return;
        }
        break;
      case RESERVATION_TYPES.FINANCE_LEASE:

        if ( pickUpDate === null || pickUpTime === null || dropOffDate === null || dropOffTime === null) {

          setErrorMessage("Fields cannot be empty");
          return;
        }
        break;

      default:
        break;
    }

    dispatch(
      updateSaveBooking(
        (props.isGuest)? null: authenticateState.customer_id, //customer id
        reservationType, //reservation type
        serviceType, //service type
        `${pickUpDate} ${pickUpTime}`, //start time
        `${dropOffDate} ${dropOffTime}`, //end time
        pickUpLocationState, //pickup location
        `${pickUpLocationCoords.latitude},${pickUpLocationCoords.longitude}`, //pick up gps
        dropOffLocationState, //drop off location
        `${dropOffLocationCoords.latitude}, ${dropOffLocationCoords.longitude}`, //dropoff gps
        travelDistance.text, //distace in km
        null, //hours
        comments, //comments
        null, //total amount
        null //selected car(s}
      )
    );
    props.history.push(`/${routePath}/available-cars`)

  };

  React.useEffect(() => {
    //get users location on map load
    _getLocationAsync();

  }, [])

  //reder polyline directions
  React.useEffect(() => {
    if (pickUpLocationCoords.latitude && dropOffLocationCoords.latitude) {
      getRouteDirections();
    }
  }, [pickUpLocationCoords, dropOffLocationCoords]);

  //map functions
  async function _getLocationAsync() {
    if (!navigator.geolocation) {
      alert('Geolocation is not supported by your browser');
      return;
    }
    navigator.geolocation.getCurrentPosition(async (location) => {

      pickUpLocationPlaceId = await getPlaceIdFromCoordinates(
        location.coords.latitude + "," + location.coords.longitude
      );

      pickUpLocationChangeHandler(pickUpLocationPlaceId);
    },
      () => alert("Error getting your location")

    )
  }

  const getPlaceIdFromCoordinates = async (coordinates) => {
    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?key=${apiKeys.google}
        &latlng=${coordinates}`
      );
      const json = await response.json();
      return json.results[0].place_id;
    } catch (error) {
      console.error(error);
    }
  };

  const onChangePickupLocationDebounced = _.debounce(async (pickUpLocation) => {
    await onChangePickupLocation(pickUpLocation);
  }, 2000);

  const onChangeDropOffLocationDebounced = _.debounce(
    async (dropOffLocation) => {
      await onChangeDropOffLocation(dropOffLocation);
    },
    2000
  );

  const onChangePickupLocation = async (pickUpLocation) => {
    const apiUrl = `https://maps.googleapis.com/maps/api/place/autocomplete/json?key=${apiKeys.google}
    &input=${pickUpLocation}
    &location=${pickUpLocationCoords.latitude},${pickUpLocationCoords.longitude}
    &radius=1000`;

    try {
      const result = await fetch(apiUrl);
      const json = await result.json();
      setPickUpLocationPredictions(json.predictions);
      // Keyboard.dismiss();
    } catch (err) {
      console.error(err);
    }
  };

  const onChangeDropOffLocation = async (dropOffLocation) => {
    const apiUrl = `https://maps.googleapis.com/maps/api/place/autocomplete/json?key=${apiKeys.google}
    &input=${dropOffLocation}
    &location=${pickUpLocationCoords.latitude},${pickUpLocationCoords.longitude}
    &radius=1000`;

    try {
      const result = await fetch(apiUrl);
      const json = await result.json();
      setDropOffLocationPredictions(json.predictions);
      // Keyboard.dismiss();
    } catch (err) {
      console.error(err);
    }
  };

  const pickUpLocationChangeHandler = async (pickUpLocationPlaceId) => {
    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/place/details/json?key=${apiKeys.google}
        &placeid=${pickUpLocationPlaceId}`
      );

      const json = await response.json();

      setPickUpLocation(json.result.name);

      setPickUpLocationCoords({
        latitude: json.result.geometry.location.lat,
        longitude: json.result.geometry.location.lng,
      });
      panTo({
        lat: json.result.geometry.location.lat,
        lng: json.result.geometry.location.lng,
      });
      // if (_map.current) {
      //   _map.current.fitToSuppliedMarkers(
      //     ["pickUpLocationMarker", "dropOffLocationMarker"],
      //     {
      //       edgePadding: { top: 100, bottom: 20, left: 20, right: 20 },
      //       // animated: true,
      //     }
      //   );
      // }
    } catch (error) {
      console.error(error);
    }
  };

  const dropOffLocationChangeHandler = async (dropOffLocationPlaceId) => {
    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/place/details/json?key=${apiKeys.google}
        &placeid=${dropOffLocationPlaceId}`
      );

      const json = await response.json();

      setDropOffLocation(json.result.name);

      setDropOffLocationCoords({
        latitude: json.result.geometry.location.lat,
        longitude: json.result.geometry.location.lng,
      });
      panTo({
        lat: json.result.geometry.location.lat,
        lng: json.result.geometry.location.lng,
      });

      // if (_map.current) {
      //   _map.current.fitToSuppliedMarkers(
      //     ["pickUpLocationMarker", "dropOffLocationMarker"],
      //     {
      //       edgePadding: { top: 100, bottom: 20, left: 20, right: 20 },
      //       // animated: true
      //     }
      //   );
      // }
    } catch (error) {
      console.error(error);
    }
  };

  //map components
  const pickUpLocationPredictionTouchableHighlight = pickUpLocationPredictions.map(
    (prediction) => (
      <MapTextPredictionComponent 
      key={prediction.id}
      text={prediction.structured_formatting.main_text}
      onClick={async () => {
        setPickUpLocationPredictions([]);
        setPickUpLocation(prediction.structured_formatting.main_text);
        pickUpLocationChangeHandler(prediction.place_id);
      }}/>
      
    )
  );

  const dropOffLocationPredictionsTouchableHighlight = dropOffLocationPredictions.map(
    (prediction) => (
      <MapTextPredictionComponent 
      key={prediction.id}
      text={prediction.structured_formatting.main_text}
      onClick={async () => {
        setPickUpLocationPredictions([]);
        setPickUpLocation(prediction.structured_formatting.main_text);
        pickUpLocationChangeHandler(prediction.place_id);
      }}/>
    )
  );

  if (pickUpLocationCoords.latitude) {
    pickUpLocationMarker = (
      <Marker
        draggable
        onDragEnd={async (e) => {
          pickUpLocationPlaceId = await getPlaceIdFromCoordinates(
            e.latLng.lat() + "," + e.latLng.lng()
          );
          pickUpLocationChangeHandler(pickUpLocationPlaceId);
        }}
        position={{ lat: pickUpLocationCoords.latitude, lng: pickUpLocationCoords.longitude }}
        title={"Pickup Location"}
        identifier={"pickUpLocationMarker"}
        maxZoomLevel={15} // default => 20
      />
    );
  }
  if (dropOffLocationCoords.latitude) {
    dropOffLocationMarker = (
      <Marker
        draggable
        onDragEnd={async (e) => {

          dropOffLocationPlaceId = await getPlaceIdFromCoordinates(
            e.latLng.lat() + "," + e.latLng.lng()
          );
          dropOffLocationChangeHandler(dropOffLocationPlaceId);
        }}
        position={{ lat: dropOffLocationCoords.latitude, lng: dropOffLocationCoords.longitude }}
        title={"Drop-off Location"}
        identifier={"dropOffLocationMarker"}
      />
    );
  }

  if (dropOffLocationDirections) {
    dropOffLocationPolyline = (
      <Polyline
        path={dropOffLocationDirections}
        options={{
          strokeColor: 'black',
          strokeOpacity: 1.0,
          strokeWeight: 2,
        }}
        strokeWidth={4}
        strokeColor="black"
      />
    );
  }

  const getRouteDirections = async () => {
    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/directions/json?key=${apiKeys.google}
        &origin=${pickUpLocationCoords.latitude},${pickUpLocationCoords.longitude}
        &destination=${dropOffLocationCoords.latitude},${dropOffLocationCoords.longitude}`
      );

      const json = await response.json();

      const points = polyline.decode(json.routes[0].overview_polyline.points);

      const destinationCoords = points.map((point) => {
        return { lat: point[0], lng: point[1] };
      });

      setDropOffLocationDirections(destinationCoords);
      setTravelDistance(json.routes[0].legs[0].distance);
      setTravelDuration(json.routes[0].legs[0].duration);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div>
      <Row>
        {/*----------- map col start ---------------*/}
        <Col
          xl={{ size: "8", order: 2 }}
          lg={{ size: "7", order: 2 }}
          md={{ size: "6", order: 1 }}
          xs={{ size: "12", order: 2 }}
          style={mapContainer}
        >
          <Row style={{ height: '100vh' }}>
            {isLoaded ? <GoogleMap
              mapContainerStyle={{
                width: "65vw",
                height: "100vh",
                //top: "1rem",
              }}
              onLoad={onMapLoad}
              options={initialRegion.options}
              center={initialRegion.center}
              zoom={initialRegion.zoom}
            >
              {dropOffLocationPolyline}
              {pickUpLocationMarker}
              {dropOffLocationMarker}
            </GoogleMap> : <Spinner />}
          </Row>
        </Col>
        {/*---------------- map col end---------- */}

        {/*--------------- details form start.-------*/}
        <Col
          xl={{ size: "4", order: 2 }}
          lg={{ size: "5", order: 2 }}
          md={{ size: "6", order: 2 }}
          xs={{ size: "12", order: 1 }}
          style={detailsStyle.container}
        >
          {/* this full height is a resposive that take the full height for 
            all browsers. all heavy lifting done */}
          <FullHeight canExceed className="col-12">
            <Row style={detailsStyle.header}>
              <h5 style={detailsStyle.headerText}>Booking Specification</h5>
            </Row>
            {/* reservation type */}
            <div style={flex}>
              <Row style={{ ...detailsStyle.inputWrapper }}>
                <Label>Reservation Type</Label>
                <Input
                  type="select"
                  name="select"
                  id="exampleSelect1"
                  style={detailsStyle.input}
                  onChange={e => {
                    setReservationType(e.target.value);
                    if (reservationType === RESERVATION_TYPES.TAXI_SERVICE) {
                      setServiceType("Chauffeur-Driven");
                    }else if (reservationType === RESERVATION_TYPES.FINANCE_LEASE) {
                      setServiceType("Self-Drive");
                    }
                  }}
                >
                  <option value={""}>Please Select</option>
                  <option value={RESERVATION_TYPES.WHOLE_DAY_IN_ACCRA}>Whole Day (Accra)</option>
                  <option value={RESERVATION_TYPES.WHOLE_DAY_OUTSIDE_ACCRA}>Whole Day (Outside Accra)</option>
                  <option value={RESERVATION_TYPES.HOURLY}>Hourly</option>
                  <option value={RESERVATION_TYPES.TAXI_SERVICE}>Taxi Service</option>
                  <option value={RESERVATION_TYPES.FINANCE_LEASE}>Financed Lease</option>
                </Input>
              </Row>

              {/* HIde all fields if Reservation type has not been selected */}

              <div style={{ display: (reservationType === "") ? "none" : "block" }}>
                {/* service type */}
                <Row style={{ ...detailsStyle.inputWrapper, display: ([RESERVATION_TYPES.TAXI_SERVICE,RESERVATION_TYPES.FINANCE_LEASE].includes(reservationType)) ? "none" : "block" }}>
                  <Label>Service Type</Label>
                  <Input
                    type="select"
                    id="exampleSelect2"
                    style={detailsStyle.input}
                    onChange={e => setServiceType(e.target.value)}
                  >
                    <option selected value={"Self-Drive"}>Self-Drive</option>
                    <option value={"Chauffeur-Driven"}>Chauffeur-Driven</option>
                  </Input>
                </Row>
                {/* pickup location */}
                <Row style={{ ...detailsStyle.inputWrapper, display: ([RESERVATION_TYPES.FINANCE_LEASE].includes(reservationType)) ? "none" : "block" }}>
                  <Label>Pick-Up Location</Label>
                  <Input
                    value={pickUpLocationState}
                    onChange={e => {
                      setPickUpLocation(e.target.value);
                      onChangePickupLocationDebounced(e.target.value);
                    }}
                    type="text"
                    name="pickupLocation"
                    placeholder="Pickup Location"
                    style={detailsStyle.inputStyle}
                  />
                  {pickUpLocationPredictionTouchableHighlight}
                </Row>
                {/* drop off location */}
                <Row style={{ ...detailsStyle.inputWrapper, display: ([RESERVATION_TYPES.FINANCE_LEASE, RESERVATION_TYPES.WHOLE_DAY_IN_ACCRA].includes(reservationType)) ? "none" : "block" }}>
                  <Label>Drop-off Location</Label>
                  <Input
                    value={dropOffLocationState}
                    onChange={e => {
                      setDropOffLocation(e.target.value);
                      onChangeDropOffLocationDebounced(e.target.value);
                    }}
                    type="text"
                    name="dropoffLocation"
                    placeholder="Drop-off Location"
                    style={detailsStyle.inputStyle}
                  />
                  {dropOffLocationPredictionsTouchableHighlight}
                </Row>
                {/* pickup date */}
                <Row style={detailsStyle.inputWrapper}>
                  <Label for="pickupDate">Pickup Date</Label>
                  <Input
                    value={pickUpDate}
                    onChange={e => setPickUpDate(e.target.value)}
                    type="date"
                    name="pickupDate"
                    placeholder="date placeholder"
                    style={detailsStyle.inputStyle}
                  />
                </Row>
                {/* pickup time */}
                <Row style={detailsStyle.inputWrapper}>
                  <Label for="pickupTime">Pickup Time</Label>
                  <Input
                    value={pickUpTime}
                    onChange={e => setPickUpTime(e.target.value)}
                    type="time"
                    name="pickupTime"
                    placeholder="time placeholder"
                    style={detailsStyle.inputStyle}
                  />
                </Row>

                {/* dropoff date */}
                <Row style={{ ...detailsStyle.inputWrapper, display: ([RESERVATION_TYPES.TAXI_SERVICE].includes(reservationType)) ? "none" : "block" }}>
                  <Label for="dropoffDate">Drop-off Date</Label>
                  <Input
                    value={dropOffDate}
                    onChange={e => setDropOffDate(e.target.value)}
                    type="date"
                    name="dropoffDate"
                    placeholder="date placeholder"
                    style={detailsStyle.inputStyle}
                  />
                </Row>

                {/* dropoff time */}
                <Row style={{ ...detailsStyle.inputWrapper, display: ([RESERVATION_TYPES.TAXI_SERVICE].includes(reservationType)) ? "none" : "block" }}>
                  <Label for="dropoffTime">Drop-off Time</Label>
                  <Input
                    value={dropOffTime}
                    onChange={e => setDropOffTime(e.target.value)}
                    type="time"
                    name="dropoffTime"
                    placeholder="time placeholder"
                    style={detailsStyle.inputStyle}
                  />
                </Row>

                {/* additional information */}
                <Row style={detailsStyle.inputWrapper}>
                  <Label for="additionalInformation">
                    Additional Information
                </Label>
                  <Input
                    value={comments}
                    onChange={e => setComments(e.target.value)}
                    type="textarea"
                    name="additinalInformation" />
                </Row>
                {/* buton */}
                <div>{errorMessage}</div>
                <Row style={detailsStyle.buttonWrapper}>
                  <Button onClick={validateMapFields} style={detailsStyle.button} >
                    continue
                </Button>
                </Row>
              </div>
            </div>
          </FullHeight>
        </Col>
        {/*------------------------ detials form end--------------------------- */}
      </Row>
    </div>
  );
}

const mapContainer = {
  height: "100vh",
};

const flex = {
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
};

const detailsStyle = {
  container: {
    // borderStyle: "solid",
    height: "100%",
    backgroundColor: colors.bg,
    display: "flex",
    flexDirection: "column",
    // alignItems: "center",
  },
  header: {
    padding: 10,
    paddingLeft: 0,
    paddingRight: 0,
    borderStyle: "solid",
    borderTopWidth: 0,
    borderBottomWidth: 0.5,
    borderLeftWidth: 0,
    borderRightWidth: 0,
    justifyContent: "center",
    borderColor: "#c4c4c4",
  },
  headerText: {
    margin: 0,
    fontSize: 18,
  },
  inputWrapper: {
    // borderStyle: "solid",
    paddingTop: 0,
    paddingBottom: 0,
    paddingLeft: 15,
    paddingRight: 15,
    marginTop: 0,
    // width: "95%",
  },

  input: {
    borderRadius: 100,
    alignSelf: "center",
  },
  inputStyle: {
    borderRadius: 100,
    paddingLeft: 10,
  },
  row: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    // borderStyle: "solid",
    width: "100%",
  },
  buttonWrapper: {
    marginTop: 20,
    marginBottom: 20,
    width: "80%",
  },
  button: {
    width: "100%",
    borderRadius: 100,
  },
};

export default BookARidePage;
