import {

   

    GET_AVAILABLE_CARS_START,
    GET_AVAILABLE_CARS_SUCCESS,
    GET_AVAILABLE_CARS_ERROR,


} from './ActionTypes';




//GET AVAILABLE CARS ACTIONS


export const getAvailableCarsStart = () => (
    {
        type: GET_AVAILABLE_CARS_START  ,           
    }
)
export const getAvailableCarsSuccess = ( categories, vehicles ) => (
    {
        type: GET_AVAILABLE_CARS_SUCCESS  ,     
        categories : categories,
        vehicles: vehicles  
        
    }
)
export const getAvailableCarsError = (errorMsg) => (
    {
        type: GET_AVAILABLE_CARS_ERROR  , 
        error: errorMsg          
    }
)
