export const logInMiddleware = (email, password) => {
    return function (dispatch) {



        dispatch(loginStart(email, password));

        return axios.post(
            URLS.AUTHENTICATE_CUSTOMER_URL,

            { email: email, password: password }

        ).then(function (response) {


            if (response.data.status === 'success') {

                //store token in async storage and go to home
                storeUserToken(response.data.token);

                //add password to customer object for later retrieval
                response.data.customer.password = password;
                
                console.log(response.data.customer);
                dispatch(loginSuccess(response.data.customer, response.data.token));
                dispatch(goToHomeScreen())

            } else {

                dispatch(loginError(response.data.message));
            }


        }).catch(function (error) {


            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(loginError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(loginError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(loginError(error.message));


            }

        });
    };

}

export const validateTokenMiddleware = (token) => {
    return function (dispatch) {



        dispatch(validateTokenStart());

        return axios.get(
            URLS.VALIDATE_TOKEN_URL,

            {
                headers: { Authorization: token },
            }

        ).then(function (response) {



            if (response.data.status === 'success') {

                //console.log("Token valid: go to homescreen");
                dispatch(validateTokenSuccess(response.data.data.customer, token))
                dispatch(goToHomeScreen())


            } else {

                //console.log("Token expired: go to login");
                dispatch(validateTokenError(response.data.message));
                dispatch(goToLoginScreen());
            }


        }).catch(function (error) {


            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(validateTokenError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(validateTokenError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(validateTokenError(error.message));


            }

        });
    };

}

