export const logOutMiddleware = () => {
    return function (dispatch) {

        dispatch(logOutStart())

        try {
            // SecureStore.deleteItemAsync('user_token');
            dispatch(logOutSuccess())
            dispatch(goToLoginScreen())
        } catch (e) {
            dispatch(logOutError())
            // clear error
        }
    }

}


