export const changePasswordMiddleware = (email, password) => {
    return function (dispatch) {



        dispatch(changePasswordStart());

        return axios.post(
            URLS.CHANGE_PASSWORD_URL,

            {
                email: email,
                password: password,
            }



        ).then(function (response) {

            // //console.log(response)
            /*
            {
                "status": "success",
                "message": "A link has been sent to the email provided. Click the link to reset your password"
            }
            */


            if (response.data.status === 'success') {


                dispatch(changePasswordSuccess(response.data.message))


            } else {

                dispatch(changePasswordError(response.data.message));
            }


        }).catch(function (error) {


            if (error.response) {

                // The request was made and the server responded with a status code
                // that falls out of the range of 2xx

                dispatch(changePasswordError(error.response.data.message));

            } else if (error.request) {

                // // The request was made but no response was received
                // // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                // // http.ClientRequest in node.js


                dispatch(changePasswordError(error.request._response));
            } else {
                // Something happened in setting up the request that triggered an Error
                dispatch(changePasswordError(error.message));


            }

        });
    };

}

